.App {
  color: white;
  display: flex;
  flex-direction: column;
  padding: 3rem;
  align-items: center;
  min-height: 100vh;
  box-sizing: border-box;
}
:root {
  --shadow-height: 0.375rem;
}
body {
  background: radial-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%));

  font-family: "Barlow Semi Condensed", sans-serif;
}
body,
html {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.rectangle {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 35rem;
  height: 5rem;
  border: 2px solid hsl(217, 16%, 45%);
  border-radius: 10px;
  padding: 1rem;
  margin: 0 1.5rem;
}
p {
  margin: 0;
}
.wordsContainer {
  line-height: 1.5rem;
  font-size: 1.7rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.scoreContainer {
  color: hsl(229, 25%, 31%);
  width: 6.688rem;
  height: 100%;
  background-color: white;
  display: flex;

  line-height: 3rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  border-radius: 7px;
  font-weight: bold;
}
.scoreContainer p {
  line-height: 1rem;
  font-weight: 600;
  letter-spacing: 0.1rem;
  font-size: 0.85rem;
  color: hsl(229, 64%, 46%);
}
.rules-button {
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 0.5rem;
  user-select: none;
  cursor: pointer;
  border: 1px solid white;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2.5rem;
  width: 8rem;
  border-radius: 7px;

  transition: 0.1s;
}
.buttons-box {
  display: grid;
  grid-template-columns: 9.5rem 9.5rem;
  column-gap: 1rem;
  position: absolute;
  bottom: 2rem;
  right: 2rem;
}
.rules-button:hover {
  transform: scale(1.1);
}
.game-board {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: relative;
}
.selections {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 4.375rem;
  row-gap: 2.125rem;
  position: relative;
  width: 23.125rem;
  height: 21.625rem;
}
.circle {
  user-select: none;
  cursor: pointer;
  box-sizing: border-box;
  width: 9.375rem; /* Ajusta el tamaño de los círculos según tus necesidades */
  height: 9.375rem; /* Ajusta el tamaño de los círculos según tus necesidades */
  border-radius: 50%;
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.circle-result {
  position: relative;

  position: static;
}

.triangle {
  z-index: -1;
  position: absolute;
  top: 4.75rem;
  width: 13.75rem;
  object-fit: contain;
  left: 4.688rem;
}
.circle-center {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px var(--shadow-height) 0px rgba(0, 0, 0, 0.2) inset;

  border-radius: 50%;
  background-color: white;
  width: calc(100% - 2rem);
  height: calc(100% - 2rem);
  --var1: 78.67%;
  width: var(--var1);
  height: var(--var1);
}
.circle-1 {
  background: linear-gradient(hsl(230, 89%, 62%), hsl(230, 84%, 63%));
  box-shadow: 0px var(--shadow-height) 0px #2947c1;
}
.circle-1:hover {
  transform: scale(1.1);
}
.circle-2 {
  background: linear-gradient(hsl(39, 89%, 49%), hsl(40, 74%, 64%));
  box-shadow: 0px var(--shadow-height) 0px rgba(198, 108, 30, 255);
}
.circle-2:hover {
  transform: scale(1.1);
}

.circle-3 {
  margin-left: 6.875rem;
  grid-column: 1 / span 2;
  grid-row: 2;
  background: linear-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
  box-shadow: 0px var(--shadow-height) 0px #9d1733;
}
.circle-3:hover {
  transform: scale(1.1);
}
.circle-4 {
  /* Lizard */
  background: linear-gradient(hsl(261, 73%, 60%), hsl(261, 72%, 63%));
  box-shadow: 0px var(--shadow-height) 0px #5e37a6;
}
.circle-4:hover {
  transform: scale(1.1);
}

.circle-5 {
  /* Spock */
  background: linear-gradient(hsl(189, 59%, 53%), hsl(189, 58%, 57%));
  box-shadow: 0px var(--shadow-height) 0px #2d8fab;
}
.circle-5:hover {
  transform: scale(1.1);
}
.top0 {
  margin-top: 1rem;
}
.advanced-text {
  font-size: 1.2rem;
  line-height: 1.2rem;
}
.pentagon-box .circle {
  position: absolute;
  --shadow-height: 0.285rem;
  width: 7.5rem;
  height: 7.5rem;
}
.pentagon-box .circle-1 {
  top: calc(50% - 5.5rem);
  right: -2rem;
}
.pentagon-box .circle-2 {
  top: -2rem;
  right: calc(50% - 3.8rem);
}
.pentagon-box .circle-3 {
  bottom: -2.5rem;
  right: calc(50% - 10rem);
}
.pentagon-box .circle-4 {
  bottom: -2.5rem;
  left: calc(50% - 10rem);
}
.pentagon-box .circle-5 {
  top: calc(50% - 5.5rem);
  left: -2rem;
}
.pentagon {
  transform: scale(1.6);
}
.circle-img {
  width: 40%;
  object-fit: contain;
}

.rules-view {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 10px;
  padding: 1.25rem;
  box-shadow: 0 0 0.625rem rgba(0, 0, 0, 0.3);
  z-index: 3;
  display: flex;
  flex-direction: column;
}

.rules-top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.rules-tittle {
  color: hsl(229, 25%, 31%);
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.8rem;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    0,
    0,
    0,
    0
  ); /* Color del overlay, ajusta la opacidad según tus preferencias */
  z-index: 2; /* Colócalo por encima de otros elementos */
  display: none; /* Inicialmente oculto */
}
.close-icon {
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
}

.rules-image {
  display: block;
  margin-top: 2.5rem;
  max-width: 100%;
  height: auto;
}

.result-container .circle {
  transform: scale(1.4);
}
.result-container {
  width: 28rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  flex-direction: row;
}
.circle-text {
  position: absolute;
  top: -2.5rem; /* Ajusta esto según tu diseño */
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.9rem;
  letter-spacing: 0.15rem;
  color: white; /* Ajusta el color según tu diseño */
  width: max-content;
}
.mirror-image {
  transform: scaleX(-1);
}
.radial-win {
  transition: 1s;
  opacity: 0;
  border-radius: 50%;
  position: absolute;
  width: 21.875rem;
  height: 21.875rem;
  z-index: -1;
  background: radial-gradient(
    rgba(255, 255, 255, 0.075) 0% 43%,
    rgba(255, 255, 255, 0.05) 43% 58%,
    rgba(255, 255, 255, 0.02) 56% 100%
  );
}
.retry-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  visibility: hidden;
  display: flex;
  opacity: 0;
  transition: 1s;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.retry-box p {
  text-align: center;
  width: 100%;
  font-weight: 700;
  font-size: 3.5rem;
}
.retry-box button {
  user-select: none;
  cursor: pointer;
  margin-top: 1rem;
  height: 3rem;
  font-size: 1rem;
  width: 12rem;
  border: none;
  background-color: white;
  color: hsl(229, 25%, 31%);
  font-weight: 700;
  border-radius: 7px;
}
.retry-box button:hover {
  color: hsl(349, 70%, 56%);
}
.mobile {
  display: none;
}
.circle:nth-of-type(2) {
  justify-self: end;
}
@media (min-width: 1440px) {
  html {
    font-size: 20px;
  }
}
@media (max-width: 780px) {
  .mobile {
    display: block;
  }
  .desktop {
    display: none;
  }
  html {
    font-size: 13px;
  }
  .rules-button {
    font-weight: 600;
    font-size: 1.3rem;
    width: 9.5rem;
    height: 3.1rem;
  }
  .buttons-box {
    left: calc(50vw - 10rem);
  }
  .circle {
    width: 9.231rem;
    height: 9.231rem;
  }
  .circle img {
    transform: scale(0.8);
  }
  .mirror-image {
    transform: scaleX(-0.8) scaleY(0.8) !important;
  }
  .triangle {
    top: 4.615rem;
    left: 4.077rem;
  }
  .triangle path {
    display: block;
    width: 9.231rem;
    height: 9.231rem;
    transform: scale(0.65);
    stroke-width: 2.308rem;
  }
  .circle-3 {
    margin-left: 7.308rem;
  }
  .rules-image {
    margin-top: 8rem;
  }
  .rules-top {
    padding: 0 1rem;
    margin-top: 3rem;
  }
  .rules-tittle {
    font-size: 2.5rem;
  }
  .close-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
  .rules-view {
    box-sizing: border-box;
    border-radius: 0;
    transform: none;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
  }
  .selections {
    width: 23.846rem;
    height: 22.308rem;
  }
  .result-container.mobile {
    height: 100%;
    display: flex;
    padding-top: 9rem;
    justify-content: space-between;
  }
  .game-board {
    flex-grow: 0;
    height: calc(100vh - 250px);
    align-items: center;
  }
  .retry-box {
    top: calc(100% - 9rem);
  }
  .circle-text {
    font-size: 1rem;
    top: initial;
    bottom: -3.077rem;
  }
  .retry-box p {
    width: 16rem;
    font-size: 4rem;
  }
  .retry-box button {
    height: 3.5rem;
    font-size: 1.1rem;
    width: 16rem;
  }
  .circle-img {
    width: 60%;
    object-fit: contain;
  }
}
